import { Box, Grid } from "@material-ui/core";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import {
  DeleteIcon,
  GiftIcon,
  PlusCircleIcon,
  VoucherIcon,
} from "../../../../../../components/SVG";
import useStyles from "./ActionPanel.styles";

const ActionPanel = () => {
  const classes = useStyles();

  return (
    <Box width="100%" paddingTop="16px" paddingLeft="3px" paddingRight="8px">
      <Box display="flex" justifyContent="space-evenly" alignItems="center">
        <Grid container>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              iconElm={PlusCircleIcon}
              customType="outlined"
              className={classes.button}
              fontSize={14}
            >
              Thm dch v (F6)
            </PosButton>
          </Grid>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              iconElm={GiftIcon}
              customType="outlined"
              className={classes.button}
              fontSize={14}
            >
              Khuyn mi (F8)
            </PosButton>
          </Grid>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              iconElm={VoucherIcon}
              customType="outlined"
              className={classes.button}
              fontSize={14}
            >
              Chit khu n (F9)
            </PosButton>
          </Grid>
          <Grid item xs={6} lg={3} className={classes.buttonContainer}>
            <PosButton
              iconElm={DeleteIcon}
              customType="outlined"
              colorType="destruction"
              className={classes.button}
              fontSize={14}
            >
              Xa tt c sp (F10)
            </PosButton>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default ActionPanel;
