import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { OrderTab } from "./types";
import { initTenant } from "../../../../global-slices/tenant-context/slice";
import { v4 as uuidv4 } from "uuid";

const initialState: OrderTab[] = [];

const tenantContextSlice = createSlice({
  name: "tabs",
  initialState: initialState,
  reducers: {
    addTab: (state: OrderTab[]) => {
      console.log("xx adding Tab in action", state.length);
      if (state.length === 0) {
        state.push({
          tabId: uuidv4(),
          tabIndex: state.length,
        });
        console.log("xx added Tab in operation", state.length);
      }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(`${initTenant}`, (state, action: PayloadAction<Date>) => {
      console.log("xx addTab in extra reducer", action);
      tenantContextSlice.caseReducers.addTab(state);
    });
  },
});

const { actions, reducer } = tenantContextSlice;
export const { addTab } = actions;
const tabsReducer = reducer;
export default tabsReducer;
