import { memo, useEffect } from "react";
import { connect, ConnectedProps } from "react-redux";
import { AppState } from "../../store";
import BoxLineItems from "./components/box-line-items";
import BoxOrderSummary from "./components/box-order-summary/BoxOrderSummary";
import OrderNavBar from "./components/nav-bar";
import useStyles from "./CreateOrders.styles";
import useHotkeys from "./hocs/hotkeys/useHotkeys";
import { updateCurrentTab } from "./slices/current-tab/slice";

const CreateOrders = (props: PropsFromRedux) => {
  const { tabs, updateCurrentTab } = props;
  const classes = useStyles();

  const hotkeyEventHandler = useHotkeys();
  useEffect(() => {
    console.log("xx tab added", tabs);
    if (tabs.length > 0) {
      updateCurrentTab(tabs[tabs.length - 1].tabId);
    }
  }, [tabs]);

  useEffect(() => {
    hotkeyEventHandler.registerOrderEventListeners();
  }, []);

  return (
    <div className={classes.root}>
      <OrderNavBar />
      <div className={classes.main}>
        <BoxLineItems />
        <BoxOrderSummary />
      </div>
    </div>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  tabs: state.tabs,
});

const connector = connect(mapStateToProps, {
  updateCurrentTab,
});
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(CreateOrders));
