import { Box, Grid } from "@material-ui/core";
import { memo, useEffect } from "react";
import { connect, ConnectedProps } from "react-redux";
import EmptyProductGraphic from "../../../../components/Graphics/EmptyProductGraphic";
import { AppState } from "../../../../store";
import useStyles, {
  columnLeftPaddings,
  columnWidths,
} from "./BoxLineItems.styles";
import ActionPanel from "./components/ActionPanel/ActionPanel";
import OrderFooter from "./components/OrderFooter/OrderFooter";
import OrderLineItem from "./components/OrderLineItem/OrderLineItem";

const BoxLineItems = (props: PropsFromRedux) => {
  const classes = useStyles();
  const { orderLineItems, theme } = props;

  useEffect(() => {
    if (orderLineItems?.length > 0) {
      setTimeout(focusrOnFirstLineItem, 0);
    }
  }, [orderLineItems?.length]);

  const focusrOnFirstLineItem = () => {
    let optionElm = document.querySelector("[data-order-line-item]") as HTMLElement;
    optionElm?.focus();
  };

  const renderHeading = () => (
    <Grid className={classes.lineItemListHeading}>
      <Box flex="1 1 0" paddingLeft="16px">
        Sn phm (
        <b
          style={{
            color: theme.current.color.text[100],
          }}
        >
          {orderLineItems?.length ?? 0}
        </b>
        )
      </Box>
      <Box
        width={columnWidths.unit}
        minWidth={columnWidths.unit}
        textAlign="right"
      >
        n v
      </Box>
      <Box
        width={columnWidths.unitPrice}
        minWidth={columnWidths.unitPrice}
        textAlign="right"
        paddingLeft={`${columnLeftPaddings.unitPrice}px`}
      >
        n gi
      </Box>
      <Box
        width={columnWidths.quantity}
        minWidth={columnWidths.quantity}
        textAlign="center"
        paddingLeft={`${columnLeftPaddings.quantity}px`}
      >
        S lng
      </Box>
      <Box
        width={columnWidths.toAmount}
        minWidth={columnWidths.toAmount}
        textAlign="right"
      >
        Thnh tin
      </Box>
      <Box width={columnWidths.delete}></Box>
    </Grid>
  );

  return (
    <Box className={classes.root}>
      <Box position="relative" flex="1 1 0" overflow="auto">
        {renderHeading()}
        {orderLineItems?.length > 0 ? (
          orderLineItems.map((lineItem, index) => (
            <OrderLineItem key={index} lineItem={lineItem} />
          ))
        ) : (
          <Box className={classes.lineItemsEmpty}>
            <EmptyProductGraphic />
          </Box>
        )}
      </Box>
      <ActionPanel />
      <OrderFooter />
    </Box>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
  currentTab: state.currentTab,
  orderLineItems: state.orderLineItems[state.currentTab],
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default memo(connector(BoxLineItems));
