import Dialog from "@material-ui/core/Dialog";
import { Box, Button } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import IconButton from "@material-ui/core/IconButton";
import { Fragment, useState, useEffect, useCallback } from "react";
import { connect, ConnectedProps } from "react-redux";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import {
  ArrowLeftIcon,
  ArrowRightIcon,
  PlusCircleOutlineIcon,
} from "../../../../../../components/SVG";
import { AppState } from "../../../../../../store";
import useStyles from "./TabOrder.style";
import { useResizeDetector } from "react-resize-detector";
import Tab from "./components/Tab/Tab";
import { v4 as uuidv4 } from "uuid";
import { useWindowSize } from "../../../../../../utils/useWindowSize";
import colors from "../../../../../../theme/statics/colors";

const ORTHER_ELEMENT_WIDTH = 900; // chiu rng ca nhng thnh phn khc trn nav_bar, tr list_order

const TabOrder = (props: PropsFromRedux) => {
  const [windowWidth, windowHeight] = useWindowSize();
  const [currentWidth, setCurrentWidth] = useState<number>(windowWidth);
  const [activeTab, setActiveTab] = useState<number>(1);
  const [trans, setTrans] = useState<number>(0);
  const [showAction, setShowAction] = useState<boolean>(false);
  const [totalWidth, setTotalWidth] = useState<number>(0);

  const classes = useStyles();
  const [deleteTab, setDeleteTab] = useState({
    tabId: "",
    open: false,
    tabIndex: 0,
  });

  const [listTab, setListTab] = useState<{ id: number; label: string }[]>([
    { id: 1, label: "Tab 1" },
    { id: 2, label: "Tab 2" },
    { id: 3, label: "Tab 3" },
    { id: 4, label: "Tab 4" },
    { id: 5, label: "Tab 5" },
    { id: 6, label: "Tab 6" },
    { id: 7, label: "Tab 7" },
    // { id: 8, label: "Tab 8" },
    // { id: 9, label: "Tab 9" },
    // { id: 10, label: "Tab 10" },
    // { id: 11, label: "Tab 11" },
    // { id: 12, label: "Tab 12" },
    // { id: 13, label: "Tab 13" },
    // { id: 14, label: "Tab 14" },
  ]);

  useEffect(() => {
    console.log("currentWidth", windowWidth - ORTHER_ELEMENT_WIDTH);
    const curWidth = windowWidth - ORTHER_ELEMENT_WIDTH - 24;
    setCurrentWidth(curWidth);
    if (totalWidth - curWidth > 0) {
      console.log("change show action true");
      setShowAction(true);
    } else {
      console.log("change show action false");
      setShowAction(false);
    }
  }, [windowWidth]);

  const handleTransLeft = () => {
    console.log("handleTransLeft", trans);
    if (trans + 196 <= 0) {
      setTrans(trans + 196);
    } else {
      setTrans(0);
    }
  };
  const handleTransRight = () => {
    console.log("handleTransRight", trans);
    if (trans - 196 >= -totalWidth + currentWidth) {
      setTrans(trans - 196);
    } else {
      setTrans(currentWidth - totalWidth);
    }
  };
  const handleCloseDialog = () => {};

  const addTab = () => {
    setListTab([
      ...listTab,
      { id: listTab.length + 1, label: `Tab ${listTab.length + 1}` },
    ]);
    console.log("new list", [
      ...listTab,
      { id: listTab.length + 1, label: `Tab ${listTab.length + 1}` },
    ]);
  };

  const removeTab = (id: number) => {
    const newList = listTab.filter((tab) => tab.id !== id);
    console.log("newList", newList);
    if (id === activeTab) {
      setActiveTab(newList[0].id);
    }
    setListTab(newList);
    // calc trans
  };

  const onResize = useCallback(
    (width: any) => {
      console.log("totalWidth", width);
      console.log("trans", trans);
      setTotalWidth(width);
      const curWidth = windowWidth - ORTHER_ELEMENT_WIDTH - 24;
      console.log(
        "totalWidth - currentWidth ",
        Number(width) - Number(curWidth)
      );
      if (width - curWidth > 0) {
        console.log("change show action true");
        setShowAction(true);
      } else {
        console.log("change show action false");
        // setTrans(0);
        setShowAction(false);
      }
      // calc trans
    },
    [windowWidth]
  );
  const { ref, width, height } = useResizeDetector({ onResize });
  // change state to props from store

  return (
    <Fragment>
      <Dialog
        open={deleteTab.open}
        onClose={handleCloseDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Use Google's location service?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Let Google help apps determine location. This means sending
            anonymous location data to Google, even when no apps are running.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <PosButton onClick={handleCloseDialog}>Disagree</PosButton>
          <PosButton onClick={handleCloseDialog} autoFocus>
            Agree
          </PosButton>
        </DialogActions>
      </Dialog>
      <Box className={classes.listWrap}>
        {showAction && (
          <Button
            className={classes.btnAction}
            style={{
              marginRight: 8,
              marginLeft: 12,
            }}
            onClick={handleTransLeft}
          >
            <ArrowLeftIcon htmlColor={colors.ink[0]} />
          </Button>
        )}
        <Box className={classes.orderListWrap}>
          <div
            ref={ref}
            className={classes.listOrder}
            style={{
              left: trans,
            }}
          >
            {listTab.map((tab, index) => {
              return (
                <Tab
                  key={index}
                  active={tab.id === activeTab}
                  onClick={() => setActiveTab(tab.id)}
                  lable={tab.label}
                  onRemove={() => removeTab(tab.id)}
                />
              );
            })}
          </div>
        </Box>
        {!!showAction && (
          <Button
            className={classes.btnAction}
            style={{
              marginLeft: 8,
            }}
            onClick={handleTransRight}
          >
            <ArrowRightIcon htmlColor={colors.ink[0]} />
          </Button>
        )}
        <IconButton onClick={addTab} className={classes.btnAdd}>
          <PlusCircleOutlineIcon />
        </IconButton>
      </Box>
    </Fragment>
  );
};

const mapStateToProps = (state: AppState) => ({
  theme: state.theme,
});

const connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;
export default connector(TabOrder);
