import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Button,
  Grid,
  Box,
} from "@material-ui/core";
import React, { useState } from "react";
import PosButton from "../../../../../../components/buttons/PosButton/PosButton";
import TextInput from "../../../../../../components/input/TextInput";
import {
  ArrowDownDoubleIcon,
  ArrowUpDoubleIcon,
  BriefcaseIcon,
  CardIcon,
  CloseIcon,
  LocationIcon,
  PhoneIcon,
  UserIcon,
} from "../../../../../../components/SVG";
import PosTypography, {
  TEXTSIZE,
} from "../../../../../../components/typography/PosTypography/PosTypography";
import { CustomerResponse } from "../../../../../../external-services/api/models/CustomerModels";
import colors from "../../../../../../theme/statics/colors";
import useStyles from "./PopupPayment.style";
import { TransitionProps } from "@material-ui/core/transitions";
import Slide from "@material-ui/core/Slide";
import PosToggle from "../../../../../../components/toggle/PosToggle";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import PaymentToggleButton from "../PaymentToggleButton/PaymentToggleButton";
import { formatPrice } from "../../../../../../utils/priceUtils";
import PosNumberInput from "../../../../../../components/numberInput/PosNumberInput";
import { ToggleButton } from "@material-ui/lab";
import TransferGraphic from "../../../../../../components/Graphics/TransferGraphic";
import OCBLogo from "../../../../../../components/Graphics/OCBLogo";
import imgQRPath from "../../../../../../utils/images/imageQR.png";
import MOMOLogo from "../../../../../../components/Graphics/MOMOLogo";
import OCGLogo from "../../../../../../components/Graphics/OCBLogo";
import VNPayLogo from "../../../../../../components/Graphics/VNPayLogo";
interface PopupPaymentProps {
  open: boolean;
  handleClose: () => void;
}
type CardInfor = {
  id: number;
  bank_name: string;
  account_name: string;
  account_number: string;
  account_QR: string;
};

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & { children?: React.ReactElement<any, any> },
  ref: React.Ref<unknown>
) {
  return <Slide direction="left" ref={ref} {...props} />;
});

const PopupPayment = (props: PopupPaymentProps) => {
  const { open, handleClose } = props;
  const [isSinglePayment, setIsSinglePayment] = useState<boolean>(true);
  const [payments, setPayments] = useState<string[]>([]);
  const [moneySuggest, setMoneySuggest] = useState<number>();
  const [card, setCard] = useState<CardInfor>();
  const classes = useStyles();
  const handleChangePayments = (
    event: React.MouseEvent<HTMLElement>,
    newFormats: string[]
  ) => {
    setPayments(newFormats);
  };
  const handleChangeCard = (
    event: React.MouseEvent<HTMLElement>,
    newFormats: CardInfor
  ) => {
    setCard(newFormats);
    console.log("card", card);
  };
  const handleChangeMoneySuggest = (
    event: React.MouseEvent<HTMLElement>,
    val: number
  ) => {
    setMoneySuggest(val);
  };
  const listPrice = [
    1234500, 1234000, 1244000, 1250000, 1300000, 1400000, 1500000, 2000000,
  ];
  const listCard: CardInfor[] = [
    {
      id: 1,
      bank_name: "BIDV",
      account_name: "Tn Khch Hng",
      account_number: "741852963548",
      account_QR: "co cc",
    },
    {
      id: 2,
      bank_name: "BIDV",
      account_name: "Tn Khch Hng",
      account_number: "741852963548",
      account_QR: "co cc",
    },
    {
      id: 3,
      bank_name: "BIDV",
      account_name: "Tn Khch Hng",
      account_number: "741852963548",
      account_QR: "co cc",
    },
  ];

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      TransitionComponent={Transition}
      fullWidth
      maxWidth="sm"
      classes={{
        paper: classes.paper,
      }}
    >
      <DialogTitle disableTypography className={classes.dialogTitle}>
        <PosTypography size={TEXTSIZE.size22} fontWeight="bold">
          Thanh ton
        </PosTypography>
        <Box>
          <PosTypography
            size={TEXTSIZE.size14}
            textColor={colors.text[90]}
            style={{ marginRight: 12 }}
          >
            Nhiu phng thc
          </PosTypography>
          <PosToggle
            // value={isSinglePayment}
            checked={!isSinglePayment}
            onChange={() => {
              setIsSinglePayment(!isSinglePayment);
              console.log("isSinglePayment", isSinglePayment);
              setPayments([]);
            }}
          />
        </Box>
      </DialogTitle>

      <DialogContent className={classes.contentWrap}>
        {/* ng chn hnh thc thanh ton */}
        <Box className={classes.payment}>
          <ToggleButtonGroup
            value={payments}
            exclusive={isSinglePayment}
            onChange={handleChangePayments}
          >
            <PaymentToggleButton
              isExclusive={isSinglePayment}
              value={"cash"}
              label={"Tin mt"}
            />
            <PaymentToggleButton
              isExclusive={isSinglePayment}
              value={"card"}
              label={"Chuyn khon"}
              icon={<TransferGraphic />}
            />
            <PaymentToggleButton
              isExclusive={isSinglePayment}
              value={"ocb"}
              label={"OCB QR"}
              icon={<OCBLogo />}
            />
            <PaymentToggleButton
              isExclusive={isSinglePayment}
              value={"vnpay"}
              label={"VN Pay"}
              icon={<VNPayLogo />}
            />
            <PaymentToggleButton
              isExclusive={isSinglePayment}
              value={"momo"}
              label={"MOMO"}
              style={{ padding: 0 }}
              icon={<MOMOLogo />}
            />
            <PaymentToggleButton
              isExclusive={isSinglePayment}
              value={"mono"}
              label={"MONO"}
            />
          </ToggleButtonGroup>
        </Box>

        <Box className={classes.paymentInfor}>
          {/* Ti khon ngn hng*/}
          <Box className={classes.cardWrap}>
            <ToggleButtonGroup
              value={card as CardInfor}
              exclusive
              onChange={handleChangeCard}
              style={{
                overflow: "hidden",
              }}
            >
              {listCard.map((c, index) => {
                return (
                  <ToggleButton
                    key={index}
                    selected={card && card.id === c.id}
                    classes={{
                      root: classes.cardItem,
                      selected: classes.cardItemSelected,
                    }}
                    value={c}
                  >
                    <img src={imgQRPath} />
                    <Box className={classes.cardInfor}>
                      <PosTypography
                        size={TEXTSIZE.size18}
                        fontWeight={"medium"}
                        className={classes.accountName}
                      >
                        {c?.bank_name} {" - "} {" Phm c Kin"}
                      </PosTypography>
                      <PosTypography
                        size={TEXTSIZE.size18}
                        textColor={colors.text[70]}
                      >
                        {c?.account_number}
                      </PosTypography>
                    </Box>
                  </ToggleButton>
                );
              })}
            </ToggleButtonGroup>
          </Box>
          {/* Khch phi tr  - always */}
          <Box className={classes.lineItem}>
            <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
              Khch phi tr
            </PosTypography>
            <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
              {formatPrice(1000000)}
            </PosTypography>
          </Box>
          {/* tr im - always*/}
          <Box className={classes.lineItem}>
            <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
              Tr im (tng im: <b style={{ color: colors.text[100] }}>60</b>
              )
            </PosTypography>
            <Box>
              <PosNumberInput
                name="quantity"
                onChange={() => {}}
                classes={classes.pointInput}
              />
              <PosTypography size={TEXTSIZE.size14} textColor={colors.text[70]}>
                im <span style={{ fontSize: 20, margin: "0 10px" }}>=</span>
                {formatPrice(1000000)}
              </PosTypography>
            </Box>
          </Box>
          {/* Tin khch a - singlePayment*/}
          {isSinglePayment && (
            <Box className={classes.lineItem} alignItems={"flex-end"}>
              <PosTypography
                size={TEXTSIZE.size16}
                fontWeight="medium"
                textColor={colors.text[100]}
              >
                Tin khch a
              </PosTypography>
              <PosNumberInput
                name="receive"
                onChange={() => {}}
                fontsize={24}
                fontWeight={500}
                style={{ width: 160 }}
              />
            </Box>
          )}
          {/* Suggest money  - singlePayment cash*/}
          {isSinglePayment && payments && payments.includes("cash") && (
            <Box>
              <ToggleButtonGroup
                value={moneySuggest}
                exclusive
                className={classes.suggest}
                onChange={handleChangeMoneySuggest}
              >
                {listPrice.map((price, index) => {
                  return (
                    <ToggleButton
                      key={index}
                      value={price}
                      classes={{
                        root: classes.suggestItem,
                        selected: classes.suggestSelected,
                      }}
                    >
                      <PosTypography textColor={colors.text[90]}>
                        {formatPrice(price)}
                      </PosTypography>
                    </ToggleButton>
                  );
                })}
              </ToggleButtonGroup>
            </Box>
          )}
          {/* Tin tha - always*/}
          <Box className={classes.lineItem} alignItems={"flex-end"}>
            <PosTypography
              size={TEXTSIZE.size16}
              fontWeight="medium"
              textColor={colors.text[100]}
            >
              Tin tha
            </PosTypography>
            <PosTypography
              size={TEXTSIZE.size24}
              fontWeight="medium"
              textColor={colors.text[100]}
            >
              0
            </PosTypography>
          </Box>
        </Box>
      </DialogContent>

      <DialogActions className={classes.dialogFooter}>
        <Box display={"flex"}>
          <Box marginLeft={"auto"}>
            <PosButton
              customSize="big"
              style={{ width: 230 }}
              fontWeight={500}
              onClick={handleClose}
              color="primary"
            >
              Hon tt
            </PosButton>
          </Box>
        </Box>
      </DialogActions>
    </Dialog>
  );
};

export default PopupPayment;
