import { TypedUseSelectorHook, useSelector } from "react-redux";
import { AppState } from "../../../../store";
import { openCustomerSearchBar, openVariantSearchBar } from "./operations";

const useTypedSelector: TypedUseSelectorHook<AppState> = useSelector;

export const useHotkeys = () => {
  const hotkeyConfig = useTypedSelector((state) => state.hotkey);
  const registerOrderEventListeners = () => {
    document.addEventListener("keydown", (evt) => {
      if (canOpenVariantSearchBar(evt)) {
        openVariantSearchBar();
      } else if (canOpenCustomerSearchBar(evt)) {
        evt.preventDefault();
        openCustomerSearchBar();
      }
    });
  };

  const canOpenVariantSearchBar = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.openVariantSearchBar;

  const canOpenCustomerSearchBar = (evt: KeyboardEvent): boolean =>
    evt.code === hotkeyConfig.order.openCustomerSearchBar;

  return {
    registerOrderEventListeners,
  };
};

export default useHotkeys;
